.burger {
  display: none;
  position: absolute;
  right: 0px;
  top: 100px;
  width: 44px;
  height: 40px;
  cursor: pointer;
  padding-left: 11px;
  outline: rgba(39, 80, 98, 0.98);
  z-index: 2;
  border: 2px solid rgba(39, 80, 98, 0.98);
  border-right: 0;
  box-sizing: border-box;
  filter: drop-shadow(2px 2px 6px rgba(0, 0, 0, 0.09));
  border-radius: 8px 0px 0px 8px;
  background-color: transparent; }
  @media (max-width: 991.98px) {
    .burger {
      display: block; } }

.main-burger {
  outline: #fff;
  border: 2px solid #fff;
  border-right: 0; }

.burger.white {
  outline: #fff;
  border: 2px solid #fff;
  border-right: 0;
  transition: 0.3s; }

.burger span,
.burger span:before,
.burger span:after {
  width: 23px;
  position: absolute;
  height: 3px;
  background: rgba(39, 80, 98, 0.98);
  display: block;
  transition: .3s;
  border-radius: 5px; }

.burger.white span,
.burger.white span:before,
.burger.white span:after {
  background: #fff; }

.main-burger.burger span,
.main-burger.burger span:before,
.main-burger.burger span:after {
  background: #fff; }

.burger span:before {
  content: "";
  top: -9px;
  transition: top 300ms 300ms, transform 300ms cubic-bezier(0.23, 1, 0.32, 1); }

.burger span {
  top: 18px; }

.burger span:after {
  content: "";
  bottom: -9px;
  transition: bottom 300ms 300ms, transform 300ms cubic-bezier(0.23, 1, 0.32, 1); }

.burger span.active:before {
  content: "";
  top: 0px;
  transform: rotate(45deg);
  transition: top 300ms, transform 300ms 300ms cubic-bezier(0.68, -0.55, 0.265, 1.55); }

.burger span.active:after {
  content: "";
  bottom: 0;
  transform: rotate(-45deg);
  transition: bottom 300ms, transform 300ms 300ms cubic-bezier(0.68, -0.55, 0.265, 1.55); }

.burger span.active {
  background: rgba(255, 255, 255, 0); }

